<template>
	<div>
		<back-header :theme="mytype"></back-header>
		<div class="content">
			<div class="noDataTips">回去吧，这里什么都没有</div>
		</div>
	</div>
</template>
<script>
	import backHeader from "@/common/backHeader.vue"
	export default{
		data(){
			return {
				mytype:""
			}
		},
		components:{
			backHeader
		},
		mounted:function(){
			this.mytype=this.$route.query.type
		},
		activated:function() {
			this.mytype=this.$route.query.type
		}
	}
</script>
<style scoped>
	
</style>


